import React, { useState, useEffect } from 'react';
import Particles from 'react-tsparticles';
import './Ticky.css';

const Ticky = () => {
  const [board, setBoard] = useState(Array(3).fill(null).map(() => Array(3).fill(null)));
  const [isXNext, setIsXNext] = useState(true);
  const [moves, setMoves] = useState([]);
  const [winner, setWinner] = useState(null);
  const [winningCells, setWinningCells] = useState([]);

  const resetGame = () => {
    setIsXNext(true);
    setMoves([]);
    setBoard(Array(3).fill(null).map(() => Array(3).fill(null)));
    setWinner(null);
    setWinningCells([]);
  };

  const handleClick = (row, col) => {
    if (winner || board[row][col]) return;

    const newBoard = board.map((r, i) =>
      r.map((c, j) => {
        if (i === row && j === col) {
          return isXNext ? 'X' : 'O';
        }
        return c;
      })
    );

    const newMove = { row, col, symbol: isXNext ? 'X' : 'O' };
    const newMoves = [...moves, newMove];

    if (newMoves.filter(move => move.symbol === 'X').length > 3) {
      const firstXMove = newMoves.findIndex(move => move.symbol === 'X');
      const { row, col } = newMoves[firstXMove];
      newBoard[row][col] = null;
      newMoves.splice(firstXMove, 1);
    }

    if (newMoves.filter(move => move.symbol === 'O').length > 3) {
      const firstOMove = newMoves.findIndex(move => move.symbol === 'O');
      const { row, col } = newMoves[firstOMove];
      newBoard[row][col] = null;
      newMoves.splice(firstOMove, 1);
    }

    setBoard(newBoard);
    setMoves(newMoves);
    setIsXNext(!isXNext);
    checkWinner(newBoard);
  };

  const checkWinner = (board) => {
    const lines = [
      // Horizontal lines
      [[0, 0], [0, 1], [0, 2]],
      [[1, 0], [1, 1], [1, 2]],
      [[2, 0], [2, 1], [2, 2]],
      // Vertical lines
      [[0, 0], [1, 0], [2, 0]],
      [[0, 1], [1, 1], [2, 1]],
      [[0, 2], [1, 2], [2, 2]],
      // Diagonal lines
      [[0, 0], [1, 1], [2, 2]],
      [[0, 2], [1, 1], [2, 0]],
    ];

    for (let line of lines) {
      const [a, b, c] = line;
      const [rowA, colA] = a;
      const [rowB, colB] = b;
      const [rowC, colC] = c;

      if (
        board[rowA][colA] &&
        board[rowA][colA] === board[rowB][colB] &&
        board[rowA][colA] === board[rowC][colC]
      ) {
        setWinner(board[rowA][colA]);
        setWinningCells([a, b, c]);
        return;
      }
    }
  };

  const renderCell = (row, col) => {
    const xMoves = moves.filter(move => move.symbol === 'X');
    const oMoves = moves.filter(move => move.symbol === 'O');

    let className = "cell";
    if (xMoves.length === 3) {
      const thirdOldestX = xMoves[0];
      if (thirdOldestX.row === row && thirdOldestX.col === col) {
        className += " highlight";
      }
    }
    if (oMoves.length === 3) {
      const thirdOldestO = oMoves[0];
      if (thirdOldestO.row === row && thirdOldestO.col === col) {
        className += " highlight";
      }
    }
    if (winningCells.some(cell => cell[0] === row && cell[1] === col)) {
      className += " winning";
    }

    return (
      <button className={className} onClick={() => handleClick(row, col)}>
        {board[row][col]}
      </button>
    );
  };

  const fireworksOptions = {
    background: {
      color: {
        value: "#F1FAEE",
      },
    },
    fpsLimit: 60,
    interactivity: {
      events: {
        onClick: {
          enable: true,
          mode: "push",
        },
        onHover: {
          enable: true,
          mode: "repulse",
        },
        resize: true,
      },
      modes: {
        bubble: {
          distance: 400,
          duration: 2,
          opacity: 0.8,
          size: 40,
        },
        push: {
          quantity: 4,
        },
        repulse: {
          distance: 200,
          duration: 0.4,
        },
      },
    },
    particles: {
      color: {
        value: ["#ff0000", "#00ff00", "#0000ff", "#ffff00", "#ff00ff"],
      },
      links: {
        color: "#ffffff",
        distance: 150,
        enable: false,
        opacity: 0.5,
        width: 1,
      },
      collisions: {
        enable: true,
      },
      move: {
        direction: "none",
        enable: true,
        outMode: "bounce",
        random: false,
        speed: 6,
        straight: false,
      },
      number: {
        density: {
          enable: true,
          area: 800,
        },
        value: 80,
      },
      opacity: {
        value: 0.5,
      },
      shape: {
        type: "circle",
      },
      size: {
        random: true,
        value: 5,
      },
    },
    detectRetina: true,
  };
  

  return (
    <div className="game">
      <div className="board">
        {board.map((row, rowIndex) =>
          row.map((cell, colIndex) => (
            <div key={`${rowIndex}-${colIndex}`} className="cell-wrapper">
              {renderCell(rowIndex, colIndex)}
            </div>
          ))
        )}
      </div>
      {winner && <div className="winner">🎉 Winner: {winner}</div>}
      <button className="reset" onClick={resetGame}>Reset</button>
    </div>
  );
};

export default Ticky;
